@import url(https://fonts.googleapis.com/css?family=BenchNine&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

body, html {
  height: 100%; }

* {
  margin: 0;
  padding: 0; }

body {
  align-items: center;
  background-color: #2D3047;
  color: #93B7BE;
  display: flex;
  font-family: 'BenchNine', sans-serif;
  font-size: 14px;
  justify-content: center; }

#container table {
  font-size: 1.2em;
  margin-bottom: 15%;
  width: 100%; }
  #container table caption {
    color: #048A81;
    font-size: 1.8em;
    font-weight: bold;
    margin-bottom: 15%; }
  #container table thead {
    color: #A799B7;
    font-weight: bold;
    font-size: 1.3em; }
  #container table tbody tr td {
    text-align: center; }
    #container table tbody tr td span {
      color: #A799B7; }

#container label {
  color: #048A81;
  font-size: 1.5em;
  text-align: center;
  text-transform: uppercase;
  width: 100%; }
  #container label select {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    background-color: transparent;
    border: 1px solid #93B7BE;
    color: #E0CA3C;
    display: block;
    font-size: 1.2em;
    margin: 10% auto;
    outline: none;
    padding: 5%; }
    #container label select:hover {
      background-color: #E0CA3C;
      border: 1px solid #A799B7;
      color: #2D3047;
      -webkit-transition: background-color .2s ease-in;
      transition: background-color .2s ease-in; }

